<template>
    <div class="manage">
        <el-button type="text" @click="handleAdd" size="mini"
            style="background-color:rgb(102,177,255);width: 65px;height: 35px; color: #fff;">+新增</el-button>
        <el-dialog title="收货地址" :visible.sync="dialogFormVisible" width="40%" :before-close="handleClose">
            <el-form :model="form" ref="form" :inline="true" :rules="rules">
                <el-form-item label="姓名" prop="name">
                    <el-input placeholder="请输入姓名" v-model="form.name"></el-input>
                </el-form-item>
                <el-form-item label="年龄" prop="age">
                    <el-input placeholder="请输入年龄" v-model="form.age"></el-input>
                </el-form-item>
                <el-form-item label="性别" prop="sex">
                    <el-select v-model="form.sex" placeholder="请选择性别">
                        <el-option label="男" :value="1"></el-option>
                        <el-option label="女" :value="0"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="出生日期" prop="birth">
                    <el-date-picker type="date" placeholder="选择日期" v-model="form.birth" style="width: 60%;"
                        value-format="yyyy-MM-dd"></el-date-picker>
                </el-form-item>
                <el-form-item label="地址" prop="addr">
                    <el-input placeholder="请输入地址" v-model="form.addr"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="sub">确 定</el-button>
            </div>
        </el-dialog>
        <el-table :data="tableData" style="width: 100%" height=90%>
            <el-table-column prop="name" label="姓名" width="180">
            </el-table-column>
            <el-table-column prop="age" label="年龄" width="180">
            </el-table-column>
            <el-table-column prop="sexList" label="性别" width="180"></el-table-column>
            <el-table-column prop="birth" label="出生日期" width="180">
            </el-table-column>
            <el-table-column prop="addr" label="地址">
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
                    <el-button size="mini" type="text" @click="open(scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="block">
            <el-pagination background layout="prev, pager, next" :total="total" @current-change="handlePage">
            </el-pagination>
        </div>
    </div>
</template>

<script>
import { getUser, createUser, updateUser, deleteUser } from '../api/index'
export default {
    data() {
        return {
            tableData: [],
            modalType: 0, //0 表示新增的弹窗  1表示编辑
            total: 0,//当前的总条数
            pageData: {
                page: 1,
                limit: 10
            },
            dialogFormVisible: false,
            form: {
                name: '',
                age: '',
                sex: '',
                birth: '',
                addr: ''
            },
            rules: {
                name: [
                    { required: true, message: '请输入姓名', trigger: 'blur' },
                ],
                age: [
                    { required: true, message: '请输入年龄', trigger: 'blur' },
                ],
                sex: [
                    { required: true, message: '请输入性别', trigger: 'blur' },
                ],
                birth: [
                    { required: true, message: '请输入日期', trigger: 'blur' },
                ],
                addr: [
                    { required: true, message: '请输入地址', trigger: 'blur' },
                ],
            },
            formLabelWidth: '120px'
        };
    },
    mounted() {
        this.getList()
    },
    methods: {
        sub() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    if (this.modalType === 0) {
                        createUser(this.form).then((res) => {
                            this.getList()
                        })
                    } else {
                        updateUser(this.form).then((res) => {
                            this.getList()
                        })
                    }
                    this.$message({ message: '提交成功', type: 'success' })
                    this.dialogFormVisible = false

                } else {
                    alert('缺少参数')
                    return false;
                }
            });
        },
        getList() {
            getUser(this.pageData).then(({ data }) => {
                this.tableData = data.list
                this.total = data.count || 0

                const sexList = data.list.map(item => {
                    return item.sex === 0 ? '女' : '男'
                })
                this.tableData.forEach((item, index) => {
                    item.sexList = sexList[index]

                })
            })
        },
        handleClose(done) {
            this.$confirm('确认关闭？')
                .then(_ => {
                    done();
                })
                .catch(_ => { });
        },
        handleAdd() {
            this.form = {
                name: '',
                age: '',
                sex: '',
                birth: '',
                addr: ''
            },
                this.dialogFormVisible = true
            this.modalType = 0
        },
        handleEdit(row) {
            this.modalType = 1
            this.dialogFormVisible = true
            this.form = JSON.parse(JSON.stringify(row))
        },
        handlePage(val) {
            this.pageData.page = val
            this.getList()
        },
        open(row) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
                center: true
            }).then(() => {
                console.log(row.id);
                deleteUser(row).then((res) => {
                    this.getList()
                })

                this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        }
    }
};
</script>

<style lang="less" scoped>
.manage {
    height: 100%;

    .el-form-item {
        margin-left: 40px;
    }

    .block {
        margin-top: 15px;
        float: right;
    }
}
</style>